<template>
  <div class="mydata" ref="mydataapp">
      <li v-for="(name, index) in names" :key="index">
          <div class="showname">
              <span>{{name}}</span>
              <span>{{data[index]}}</span>
          </div>
          <div class="showdata" :style="{width: width+'px'}">
              <span class="dbg" :style="{backgroundColor: colorx}"></span>
              <span class="dd" :style="{width: length[index]+'%', backgroundColor: color[index]}"></span>
          </div>
      </li>
  </div>
</template>

<script>
const xs = 3000;

export default {
    name: 'DisplayDig',
    data() {
        return {
            width: 350,
            names: ['血量', '物攻', '物防', '特攻', '特防', '速度'],
            data: [1000, 1000, 1000, 1000, 1000, 1000],
            color: ['#ae2012', '#ee9b00', '#EBC268', '#4895ef', '#0077b6', '#7b2cbf'],
            length: [30, 30, 30, 30, 30, 30],
            colorx: '#623838',
        }
    },
    methods: {
        async fetchData(e) {
            if (e) {
                this.data = e;
                let tmpmax = Math.max(...e);
                let expected = Math.ceil(tmpmax/xs)*xs;
                let percentage = e.map(x=>Math.round(x/expected*1000)/10);
                this.length = percentage;
            }
        }
    },
    watch: {
        '$store.getters.allvalue': function(e) {
            this.fetchData(e);
        },
    },
    mounted() {
        this.$bus.$on('changebarcolor', (d) => {
            this.color = d;
        })
        this.width = this.$refs.mydataapp.clientWidth - 100;
        window.addEventListener('resize', () => {
            this.width = this.$refs.mydataapp.clientWidth - 100;
        })
        this.fetchData();
    },
    beforeDestroy() {
        this.$bus.$off('changebarcolor');
    },
}
</script>

<style scoped>
.mydata {
    width: 100%;
}
.mydata li {
    display: flex;
    list-style: none;
    width: 100%;
    box-sizing: border-box;
    justify-content: space-between;
    height: 2em;
}
.showname {
    width: 5em;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.showname span:first-child {
    margin-right: 0.2em;
}
.showname span {
    display: block;
}
.showdata {
    position: relative;
    display: block;
}
.showdata span {
    height: 18px;
    border-radius: 15px;
    display: block;
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
}
.dbg {
    width: 100%;
}
.dd {
    box-shadow: 3px 0 3px -2px #807e7e8c;
    transition: 0.5s;
}
</style>