<template>
    <label class="switch" >
        <input type="checkbox" v-model="checking" @change="sendD" >
        <span class="slider round"></span>
    </label>
</template>

<script>
export default {
    name: 'MySwitch',
    props: ['swdata', ],
    data() {
        return {
            checking: false,
        }
    },
    mounted() {
        this.checking = this.swdata.default || false;
        this.sendD();
    },
    methods: {
        sendD() {
            this.$store.dispatch('switch_val', [this.swdata.short, this.checking]);
            if (this.swdata.short === 'buildup') {
                this.$bus.$emit('showing_build', this.checking);
            }
        }
    }
}
</script>

<style scoped>
/* The switch - the box around the slider */
.switch {
    position: relative;
    display: inline-block;
    width: 38px;
    height: 10px;
}

/* Hide default HTML checkbox */
.switch input {
    opacity: 0;
    width: 0;
    height: 0;
}

/* The slider */
.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: var(--l4);
    box-shadow: 0 0 1px 1px rgb(93 93 93 / 32%);
    -webkit-transition: .2s;
    transition: .2s;
}

/* the round */
.slider:before {
    position: absolute;
    content: "";
    height: 16px;
    width: 16px;
    left: 0px;
    bottom: -3px;
    background-color: var(--l10, 'white');
    -webkit-transition: .2s;
    transition: .2s;
}

input:checked + .slider {
    background-color: var(--l0, '#2196F3');
}

input:checked + .slider:before {
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
    transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
    border-radius: 20px;
}

.slider.round:before {
    border-radius: 50%;
}
</style>