<template>
    <div>
        <ul>
            <li class="oneLine" v-for="(config, index) in configs" :key="index + baseT">
                <span>{{config.title}}</span>
                <template v-if="config.type === 'radio'">
                    <Radio :rdata="config.details" :limit="false" :big="true" />
                </template>
                <template v-else-if="config.type === 'switch'">
                    <MySwitch :swdata="config.details"/>
                </template>
                <template v-else-if="config.type === 'switchSK'">
                    <MySwitch :swdata="config.details"/>
                </template>
                <template v-else-if="config.type === 'select'">
                    <SelectPane :sdata="config.details" />
                </template>
                <template v-else-if="config.type === 'switchx'">
                    <Guild/>
                    <MySwitch :swdata="config.details"/>
                </template>
                <template v-else-if="config.type === 'radio2'">
                    <Radio :rdata="config.details" :limit="2" />
                </template>
                <template v-else-if="config.type === 'switch_more'">
                    <MySwitch :swdata="config.details"/>
                    <div class="break"></div>
                    <div class="newconfig" v-if="enable_build">
                        <CB v-for="(item, index) in config.details.text" :key="index" :cdata="item"/>
                    </div>
                </template>
            </li>
        </ul>
        <ul class='an'>
            <li v-for="(item, index) in announcements" :key="index">
                {{item}}
            </li>
        </ul>
    </div>
</template>

<script>
import Radio from '/src/components/assets/radioButton.vue'
import MySwitch from '/src/components/assets/switch.vue'
import SelectPane from '/src/components/assets/selectPane.vue'
import CB from '/src/components/assets/controlButton.vue'

import Guild from './guild.vue'

import { item, Xitem } from '/src/components/item.js';

const item_idx = 6; // if any changes

export default {
    name: 'PmConfig',
    components: {
        Radio, MySwitch, SelectPane, Guild, CB, 
    },
    data() {
        return {
            baseT: new Date().getTime(), 
            enable_build: false,
            configs: [
                {
                    title: '突破',
                    type: 'radio',
                    details: {
                        short: 'bt',
                        text: ['橙3', '红', '红1'],
                        checked: 1,
                    }
                }, {
                    title: '特训',
                    type: 'radio',
                    details: {
                        short: 'training',
                        text: ['特0', '特1', '特2'],
                        checked: 1,
                    }
                }, {
                    title: '亲密度',
                    type: 'switch',
                    details: {
                        short: 're',
                        default: true,
                    }
                }, {
                    title: '契约',
                    type: 'switch',
                    details: {
                        short: 'ct',
                        default: true,
                    }
                }, {
                    title: '体质',
                    type: 'switch',
                    details: {
                        short: 'co',
                        default: false,
                    }
                }, {
                    title: '公会',
                    type: 'switchx',
                    details: {
                        short: 'gh',
                        default: true,
                    }
                }, {
                    title: '道具',
                    type: 'select',
                    details: {
                        short: 'item',
                        names: [],
                    }
                }, {
                    title: '性格',
                    type: 'select',
                    details: {
                        short: 'character',
                        names: ['平衡', '开朗(速度↑特攻↓)', '固执(物攻↑特攻↓)', '胆小(速度↑物攻↓)', '保守(特攻↑物攻↓)', 
                            '勇敢(物攻↑速度↓)', '冷静(速度↑速度↓)', '淘气(物防↑特攻↓)', '慎重(特防↑特攻↓)', 
                            '大胆(物防↑物攻↓)', '沉着(特防↑物攻↓)', '悠闲(物防↑速度↓)', '狂妄(速度↑速度↓)', 
                            '急躁(速度↑物防↓)', '天真(速度↑特防↓)', '孤独(物攻↑物防↓)', '调皮(物攻↑特防↓)', 
                            '稳重(特攻↑物防↓)', '马虎(特攻↑特防↓)', '温顺(特防↑物防↓)', '乐天(物防↑特防↓)'],
                    }
                }, {
                    title: '努力值',
                    type: 'radio2',
                    details: {
                        short: 'effort',
                        text: ['血量', '物攻', '物防', '特攻', '特防', '速度'],
                    }
                }, {
                    title: '强化',
                    type: 'switch_more',
                    details: {
                        short: 'buildup',
                        default: false,
                        text: [ {
                            name: '物攻',
                            id: 'ak',
                            default: 0,
                            min: -6,
                            max: 6,
                            step: 1,
                            showas: 'number',
                        }, {
                            name: '物防',
                            id: 'df',
                            default: 0,
                            min: -6,
                            max: 6,
                            step: 1,
                            showas: 'number',
                        }, {
                            name: '特攻',
                            id: 'sa',
                            default: 0,
                            min: -6,
                            max: 6,
                            step: 1,
                            showas: 'number',
                        }, {
                            name: '特防',
                            id: 'sd',
                            default: 0,
                            min: -6,
                            max: 6,
                            step: 1,
                            showas: 'number',
                        }, {
                            name: '速度',
                            id: 'sp',
                            default: 0,
                            min: -6,
                            max: 6,
                            step: 1,
                            showas: 'number',
                        }, ]
                    }
                }, 
            ], 
            announcements: [
                '以上数据误差仅存在于小数点取整时',
                '公会加成采用了游戏中种族继承的算法，是完全正确的',
                '如果出错，那可能是我录数据时出错了，请通知我',
                '移动端适配了返回键，只要你的浏览器支持',
            ]
        }
    },
    created() {
        let tmp = [];
        for (let i of item) {
            tmp.push(new Object({
                iid: i.iid, 
                name: i.name,
            }));
        }
        this.configs[item_idx].details.names = tmp;
    },
    async mounted() {
        this.$bus.$on('showing_build', (e) => {
            this.enable_build = e;
        });
        try {
            let x = this.$store.state.pid;
            if (x) {
                for (let i of Xitem) {
                    if (i.target.indexOf(x) >= 0) {
                        this.configs[item_idx].details.names.splice(1, 0, (new Object({
                            iid: i.iid,
                            name: i.name,
                            // target: i.target,
                        })))
                    }
                }
            }
            if (this.$store.state.has_skin) {
                this.configs.splice(item_idx+1,0, new Object({
                    title: '皮肤',
                    type: 'switchSK',
                    details: {
                        short: 'skin',
                        default: false,
                    }
                }))
            }
        } catch(e) {
            console.log('async err in mounted img', e);
        }
    }

}
</script>

<style scoped>
ul {
    margin: 0;
    padding: 0;
}
.oneLine {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-between;
    min-height: 2em;
    margin: 0.2em 0.8em;
    border-bottom: solid 1px var(--bgL, '#bfbfbfa3');
}

.break {
  flex-basis: 100%;
  height: 0;
}
.newconfig {
    display: flex;
    width: 100%;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
    box-sizing: border-box;
    padding: 0.4em 0;
}
.an {
    color: var(--d9);
    margin: 1em 0 2em 0;
    line-height: 1.6em;
}
</style>