<template>
    <div>
        <select class="myselect" v-model="selected" @change="select_change" :multiple="sdata.short === 'ty'">
            <template v-if="sdata.short === 'item'">
                <option v-for="option in sdata.names" :value="option.iid" :key="option.iid">{{option.name}}</option>
            </template>
            <template v-else>
                <option v-for="(option, index) in sdata.names" :value="index" :key="index">{{option}}</option>
            </template>
        </select>
    </div>
</template>

<script>
const itemDefault = 'u00';

export default {
    name:'SelectPane',
    props: ['sdata'],
    data() {
        return {
            selected: 0,
        }
    },
    methods: {
        select_change() {
            if (this.sdata.short === 'item') { // 携带道具
                // console.log("selected", this.selected, this.sdata);
                this.$store.dispatch('itemwhich', this.selected);
            } else if (this.sdata.short === 'character') { // 性格
                this.$store.dispatch('character', +this.selected);
            } else if (this.sdata.short === 'discount') { // 折扣
                this.$bus.$emit('discount_filter', +this.selected);
            }
            // console.log("se", this.sdata.short, this.selected);
        }
    },
    watch: {
        "$route.query.id"() {
            if (this.sdata.short === 'item' || this.sdata.short === 'character') {
                this.selected = 0;
                if (this.sdata.short === 'item') this.selected = itemDefault;
                this.select_change();
            }
        }
    },
    // beforeMount() {
    //     if (this.sdata.short === 'item') {


    //     }
    // },
    mounted() {
        if (this.sdata.short === 'item') {
            this.selected = itemDefault;
        }
        this.$bus.$on('clearAll', () => { // 针对 discount
            if (this.sdata.short === 'discount') {
                if (+this.selected !== 0) {
                    this.selected = 0;
                    this.$bus.$emit('discount_filter', 0);
                }
            }
        })
    },
    beforeDestroy() {
        this.$bus.$off('clearAll')
    }
}
</script>

<style scoped>
.myselect {
    outline: none;
    font-size: 15px;
    border-radius: 16px;
    border: 1px solid #6b676794;
    color: var(--d10);
    background-color: var(--l8, '#e4e4e4');
}
.myselect:focus {
    outline-width: 0;
}
</style>