<template>
    <div class="wholePane">
        <div class="leftPane" :style="{width: $store.getters.ifPC ? '40%' : '100%'}">
            <PmList />
        </div>
        <div class="rightPane" :style="{width: $store.getters.ifPC ? '60%' : '100%' }"
          v-show="$store.getters.ifPC || !isHide">
            <!-- <PmDetail /> -->
            <router-view v-if="routerAlive"></router-view>
        </div>
    </div>
</template>

<script>
import json from './assets/pokemondata.json';

import PmList from './pm_list/ListBody.vue'

// import PmDetail from './pm_panel/pmDetail.vue'
const dis = ['任何', 10, 9.8, 9, 8, 6.5, 5.5];

function mylistdata() {
    let newdata = [];
    let i = 0;
    json.forEach(e => {
        newdata.push({
            pid: e.no,
            img: e.no + '_' + e.ename + '_' + e.appearance + '.png',
            types: e.types.split(','),
            name: e.name,
            oname: e.oname,
            ename: e.ename,
            discount: e.discount,
            date: e.updated,
            appearance: e.appearance,
            lid: i,
        });
        i++;
    });
    return newdata;
}
const d1 = mylistdata();

export default {
    components: {
        // PmDetail,
        PmList,
    },
    data() {
        return {
            routerAlive: true,
            isHide: true,
            json: json, // original data
            lastSort: 0, // for sort
            lijson: d1, // processed data for list
            copyli: d1, // copy list, for changes
            // rc: "#b7dfd0", // right pane default background color
        }
    },
    methods: {
        filterData(d) {
            if (d === '') this.copyli = this.lijson;
            else {
                this.copyli = this.lijson.filter((e) => {
                    return e.name.indexOf(d) >= 0 || e.ename.indexOf(d) >= 0
                        || e.oname.indexOf(d) >= 0;
                })
            }
            this.sortData(this.lastSort);
        },
        filterType(d) {
            if (d === '任何') {
                this.copyli = this.lijson;
            } else {
                let tmpset = new Set(...[d.split(',')]);
                this.copyli = this.lijson.filter((e) => {
                    return tmpset.has(e.types[0]) || (e.types[1] && tmpset.has(e.types[1]))
                })
            }
            this.sortData(this.lastSort);
        },
        filterDiscount(d) {
            if (+d === 0) {
                this.copyli = this.lijson;
            } else {
                this.copyli = this.lijson.filter((e) => {
                    return +e.discount === dis[+d];
                })
            }
            this.sortData(this.lastSort);
        },
        sortData(k) {
            this.lastSort = k;
            this.copyli.sort((a,b) => {
                if (k === 0) return +b.date - +a.date;
                else if (k === 1) return +a.date - +b.date;
                else if (k === 2) return b.pid < a.pid ? -1: 1;
                else return a.pid < b.pid ? -1: 1;
            })
        },
        getPM(k) {
            let temp = json.filter(d=>d.no===k)[0];
            // console.log(k, temp)
            let g_data = new Object({
                img: temp.no + '_' + temp.ename + '_' + temp.appearance + '.png',
                valM: temp.value_M.split(',').map(a=>+a),
                val0: temp.value_0.split(',').map(a=>+a),
                constitution: temp.constitution.split(',').map(a=>+a),
                // val: [temp.hp, temp.pa, temp.pd, temp.sa, temp.sd, temp.speed],
                // bt1: temp.bt1,
                // st1: temp.st1,
                // st2: temp.st2,
                // has_item: temp.has_item,
            })
            if (temp.skin_value) {
                g_data.skin = temp.skin_value.split(',').map(a=>+a);
                g_data.skin.push(0);
                g_data.has_skin = true;
            } else {
                g_data.skin = [0,0,0,0,0,0];
                g_data.has_skin = false;
            }
            this.$store.dispatch('all_data', g_data); // sending all data to store

            let t_data = new Object({
                name: temp.name,
                types: temp.types.split(','),
            })
            // this.$bus.$emit('wantedData', g_data);
            this.$bus.$emit('wantedTitle', t_data);
        },
        routerA() {
            this.$nextTick(() => {
                this.routerAlive = true;
            })
            this.routerAlive = false;
        }
    },
    watch: {
        copyli() {
            this.$bus.$emit('listjson', this.copyli);
        },
        '$route.name'() {
            if (this.$route.name === 'pmdetails') {
                // console.log(this.$route)
                this.getPM(this.$route.query.id);
                this.isHide = false;
            } else if (this.$route.name === 'home') {
                this.isHide = true;
            }
        },
        $route() {
            if (this.$route.name === 'pmdetails') {
                this.getPM(this.$route.query.id);
            }
        }
    },
    mounted() {
        this.$bus.$emit('listjson', this.copyli);
        this.$bus.$on('inputchange', (d) => {
            this.filterData(d);
        });
        this.$bus.$on('sortit', (d) => {
            this.sortData(+d);
        });
        this.$bus.$on('forPm', (d) => {
            this.getPM(d);
        });
        this.$bus.$on('type_filter_res', d => {
            this.filterType(d);
        })
        this.$bus.$on('discount_filter', d => {
            this.filterDiscount(d);
        })

        this.routerA();
        this.$bus.$on('show_right', () => {
            this.isHide = false;
        });
    },
    beforeDestroy() {
        this.$bus.$off('inputchange');
        this.$bus.$off('sortit');
        this.$bus.$off('forPm');
        this.$bus.$off('inputtype_filter_reschange');
        this.$bus.$off('discount_filter');
        this.$bus.$off('show_right');
    }
}
</script>

<style scoped>
.wholePane {
    width: 100%;
    height: 100%;
    display: block;
    position: relative;
    overflow: hidden;
}
.leftPane {
    position: absolute;
    left: 0;
    display: block;
    width: 40%;
    height: 100%;
    background-color: var(--l4, '#a8c0ec');
}
.rightPane {
    position: absolute;
    right: 0;
    display: block;
    width: 60%;
    height: 100%;
    background-color: var(--l3, '#b7dfd0');
    overflow: auto;
}
.testcolor {
    position: relative;
    display: block;
    padding: 10px;
}
</style>