<template>
    <div class="most_outer">
        <span class="item_name">{{cdata.name}}</span>
        <div class="outer">
            <button @click="changeV(0)">-</button>
            <span>{{toPercent(cvalue, cdata.showas)}}</span>
            <button @click="changeV(1)">+</button>
        </div>
    </div>
</template>

<script>
const build_id = ['ak', 'df', 'sa', 'sd', 'sp'];

export default {
    name: 'CtrlBtn',
    props: ['cdata'],
    data() {
        return {
            cvalue: this.cdata.default
        }
    },
    watch: {
        cvalue() {
            if (this.cdata.id === 'attack' || this.cdata.id === 'defend') {
                let x = localStorage.getItem('gh');
                if (x !== null) {
                    let y = JSON.parse(x);
                    if (this.cdata.id === 'attack') {
                        y.a = this.cvalue;
                    } else if (this.cdata.id === 'defend') {
                        y.d = this.cvalue;
                    }
                    localStorage.setItem('gh', JSON.stringify(y));
                    this.$store.commit('GH', y);
                }
            } else {
                for (let i of build_id) {
                    if (i === this.cdata.id) {
                        this.$store.dispatch(i, this.cvalue);
                    }
                }
            }
            
            
        }
    },
    methods: {
        toPercent(t, a) {
            if (a === 'percent') {
                return (Math.round((+t)*100)).toString()+'%';
            } else if (a === 'number') {
                if (+t > 0) {
                    return '+' + t.toString()
                }
            }
            return t;
        },
        getRound(a) {
            return Math.round((+a)*100)/100;
        },
        changeV(d) {
            if (d === 0) { // sub
                this.cvalue = this.getRound((+this.cvalue) - (+this.cdata.step));
                if (+this.cvalue < +this.cdata.min) {
                    this.cvalue = +this.cdata.min;
                }
            } else { // add
                this.cvalue = this.getRound((+this.cvalue) + (+this.cdata.step));
                if (+this.cvalue > +this.cdata.max) {
                    this.cvalue = +this.cdata.max;
                }
            }
        }
    }
}
</script>

<style scoped>
.most_outer {
    display: inline-flex;
    background-color: var(--d2, '#388dd7');
    padding: 0.1em 0.2em;
    margin: 0 0.2em;
    border-radius: 15px;
}
.item_name {
    color: white;
    margin: 0 4px;
}
.outer {
    display: inline-flex;
    align-items: center;
    flex-direction: row;
    align-items: center;
    flex-direction: row;
    background-color: var(--l3,'#a0cae3');
    padding: 0.1em;
    border-radius: 15px;
}
.outer button {
    border-radius: 50%;
    border: none;
    width: 1.6em;
    height: 1.6em;
    display: inline-block;
    background-color: var(--d2, '#2d73cb');
    color: white;
    cursor: pointer;
}
.outer span {
    display: inline-block;
}

</style>