        </div>
    </div>
</template>

<script>
import UserInput from './UserInput.vue'
import MyList from './MyList.vue'

export default {
    name: 'ListBody',
    data() {
        return {
            i_height: 0,
            computedHeight: 0,
            pHeight: 0,
            isDark: false,
        }
    },
    components: {
        UserInput,
        MyList,
    },
    methods: {
        theInputHeight(h) {
            this.i_height = h;
        },
        subchange() {
            this.$bus.$emit('darktoggleclicked');
        }
    },
    watch: {
        computedHeight(newval) {
            this.pHeight = newval - this.i_height;
        }
    },
    mounted() {
        // this.isDark = window.localStorage.getItem('isdark') || false;
        this.computedHeight = this.$el.clientHeight;
        this.$bus.$on('toggledark', (e) => {
            this.isDark = e;
        })
    },
    beforeDestroy() {
        this.$bus.$off('toggledark');
    },
}
</script>

<style>
#dark_toggle {
    display: flex;
    position: absolute;
    right: 10px;
    bottom: 10px;
    background-color: #372f2f73;
    cursor: pointer;
    border-radius: 50%;
    width: 50px;
    height: 50px;
    text-align: center;
    align-items: center;
}
#dark_toggle svg {
    display: block;
    width: 30px;
    height: 30px;
    margin: auto;
}
</style>