<template>
    <div class="title_div" :class="$store.getters.ifPC? '':'phone_title'">
        <!-- go back if on mobile -->
        <div v-show="!$store.getters.ifPC" class="arrow_outer" @click="goBack"><i class="arrow"></i></div>
        <span class="pm_name" :class="$store.getters.ifPC? 'bookmark':'bookcenter'">{{pname}}</span>
        <div class="pm_type">
            <span class="ty" v-for="(ty, index) in ptype" :key="index"
              :style="{backgroundColor: tc.get(ty)}">{{ty}}</span>
        </div>
    </div>
</template>

<script>
import { type_color } from '../colorSet'

export default {
    name: 'pm_top',
    data() {
        return {
            tic: 'beige',
            pname: '',
            ptype: '',
            tc: type_color,
        }
    },
    methods: {
        goBack() {
            // history.back();
            // this.$bus.$emit('router_alive');
            this.$router.replace({
                name: 'home',
            })
            // console.log(this.$router);
        }
    },
    mounted() {
        this.$bus.$on('wantedTitle', (d) => {
            // console.log('title aha');
            this.pname = d.name,
            this.ptype = d.types;
        })
        // this.$bus.$on('title_color', (d) => {
        //     this.tic = d;
        // })
    },
    beforeDestroy() {
        this.$bus.$off('wantedTitle')
    }
}
</script>

<style scoped>
.phone_title {
    position: fixed;
    left: 0;
    top: 0;
    z-index: 2000;
    background-color: var(--d7, 'darkblue');
}
.title_div {
    display: flex;
    text-align: center;
    width: 100%;
    align-content: center;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 0.4em;
    box-sizing: border-box;
}
.pm_name, .pm_type {
    display: block;
    margin: 2px;
}

.pm_name {
    font-size: 20px;
}
.bookmark {
    min-width: 3em;
    padding: 0 1em;
    color: var(--d9, 'rgb(51, 51, 51)');
    display: inline-flex;
    height: 0px;
    border: 1em solid;
    border-color: var(--l7, 'beige');
    border-left-color: transparent !important;
    border-right-color: transparent !important;
    content: '';
    box-shadow: 0 5px 5px -5px #000;
    align-items: center;
    justify-content: center;
}
.bookcenter {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    color: white;
    width: fit-content;
}

.ty {
    margin-left: 0.4em;
    display: inline-block;
    width: 3em;
    color: white;
    border-radius: 5px;
}


/* go back arrow */
.arrow_outer {
    display: flex;
    align-items: center;
    align-content: center;
    cursor: pointer;
}
.arrow {
    margin-left: 0.5em;
    border: solid white;
    border-width: 0 2px 2px 0;
    display: inline-block;
    padding: 5px;
    transform: rotate(135deg);
    -webkit-transform: rotate(135deg);
}
</style>