<template>
  <div>
        <div class="showed" @click="showit">{{showed}}</div>
        <div v-show="showSelection" >
            <ul class="show_ul">
                <li v-for="(item, index) in mdata.names" :key="index">
                    <input class="c_ipt" type="checkbox" :value="index" :id="item" v-model="se" />
                    <label class="c_label" :for="item">{{item}}</label>
                </li>
                <div class="filter_choice">
                    <button @click="selectall(1)">全选</button>
                    <button @click="selectall(0)">全不选</button>
                    <button @click="selectall(-1)">关闭</button>
                </div>
            </ul>
        </div>
  </div>
</template>

<script>
export default {
    name: 'MultiSelection',
    props: ["mdata"],
    data() {
        return {
            se: [],
            showSelection: false,
        }
    },
    computed: {
        showed() {
            let toshow = '';
            if (this.se.length === 0 || this.se.length === this.mdata.names.length) {
                toshow = '任何';
            } else {
                toshow = this.se.map(e=>this.mdata.names[e]).join(',')
            }
            this.$bus.$emit('type_filter_res', toshow);
            return toshow;
        }
    },
    methods: {
        showit() {
            this.showSelection = !this.showSelection;
        },
        hide() {
            if (this.showSelection)
                this.showSelection = false;
        },
        selectall(e) {
            if (e === 1) {
                this.se = [];
                for (let i = 0; i < this.mdata.names.length; i++) {
                    this.se.push(i);
                }
            } else if (e === 0) {
                this.se = [];
            } else {
                this.showSelection = false;
            }
        }
    },
    mounted() {
        this.$bus.$on('clearAll', () => {
            this.se = [];
        })
    },
    created() {
        window.addEventListener('click', (e) => {
            if (!this.$el.contains(e.target)){
            this.showSelection = false
            }
        })
    },
    beforeDestroy() {
        this.$bus.$off('clearAll')
    }
}
</script>

<style scoped>
.showed {
    cursor: pointer;
    color: var(--l10);
    background-color: var(--d3, 'skyblue');
    font-size: 0.8em;
    padding: 0.1em 0.5em;
    border-radius: 10px;
    max-width: 100px;
    overflow-wrap: break-word;
}
.show_ul {
    display: flex;
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    z-index: 1000;
    background-color: var(--l7, 'beige');
    padding: 0;
    width: 23em;
    flex-direction: row;
    align-items: center;
    align-content: center;
    flex-wrap: wrap;
    justify-content: center;
}
.show_ul li {
    position: relative;
    display: block;
    cursor: pointer;
    margin: 0.8em 0.3em;
    width: 3em;
}
.c_label {
    position: relative;
    cursor: pointer;
    background-color: var(--l4, 'skyblue');
    display: block;
    border-radius: 5px;
    color: var(--d10);
}
.c_ipt {
    width: 0;
    height: 0;
    opacity: 0;
    position: absolute;
    left: 0;
    top: 0;
}
.c_ipt:checked + .c_label {
    color: var(--l10);
    background-color: var(--d3, 'dodgerblue');
}

.filter_choice {
    display: flex;
    align-content: center;
    align-items: center;
    justify-content: space-evenly;
    padding: 2em 0.8em;
    width: 100%;
}
.filter_choice button {
    display: block;
    border: none;
    border-radius: 10px;
    padding: 0.2em 0.5em;
    color: var(--l10);
    background-color: var(--d4, 'dodgerblue');
    cursor: pointer;
}
</style>