<template>
    <div class="mybody" :class="$store.getters.ifPC? '':'phone_body'">
        <img class="bodyimg" :src="img" @load="loadImage" />
        <div id="chart-container">
            <!-- <ShowData /> -->
            <DisplayDig />
        </div>
        <div>
            <PmConfig/>
        </div>
    </div>
</template>

<script>
import DisplayDig from './displayDig.vue'
// import ShowData from './showData.vue'
import PmConfig from './pmConfig.vue'

import getAverageRGB from './ave_color.js';

export default {
    name: 'pm_body',
    components: {
        PmConfig, DisplayDig, //ShowData,
    },
    data() {
        return {
            img: '',
            // img: require('@/assets/logo.png'),
        }
    },
    watch: {
        '$store.state.img'() {
            this.img = '/imgX/' + this.$store.state.img;
        },
    },
    methods: {
        loadImage(e) {
            this.$bus.$emit('dy_color', getAverageRGB(e.target));
        }
    },
    async mounted() {
        try {
            let x = this.$store.state.img;
            this.img = '/imgX/' + x;
        } catch(e) {
            console.log('async err in mounted img', e);
        }
    },

}
</script>

<style scoped>
.phone_body {
    margin-top: 3em;
}
.mybody {
    position: relative;
    width: 100%;
    /* should be removed */
    min-height: 400px;

    box-sizing: border-box;
}
.bodyimg {
    max-width: 98%;
}

#chart-container {
    margin: auto;
    padding: 0.8em;
    background-color: var(--l8, 'white');
    border-radius: 15px;
    box-sizing: border-box;
    width: 95%;
    box-shadow: 1px 1px 3px 1px #8585852e;
    margin-bottom: 1em;
}
</style>