export default function getAverageRGB(imgEl) {

    var blockSize = 10, // only visit every 5 pixels
        defaultRGB = {r:0,g:0,b:0}, // for non-supporting envs
        canvas = document.createElement('canvas'),
        context = canvas.getContext && canvas.getContext('2d'),
        data, width, height,
        i = -4,
        length,
        rgb = {r:0,g:0,b:0};

    if (!context) {
        return defaultRGB;
    }

    height = canvas.height = imgEl.naturalHeight || imgEl.offsetHeight || imgEl.height;
    width = canvas.width = imgEl.naturalWidth || imgEl.offsetWidth || imgEl.width;

    context.drawImage(imgEl, 0, 0);

    try {
        data = context.getImageData(Math.round(0.3*width), Math.round(0.3*height),
                Math.round(0.7*width), Math.round(0.7*height));
    } catch(e) {
        /* security error, img on diff domain */
        return defaultRGB;
    }

    length = data.data.length;

    var count = [0, 0, 0];
    while ( (i += blockSize * 4) < length ) {
        if (data.data[i] >=250 && data.data[i+1] >= 250 && data.data[i+2] >= 250) {
            continue;
        }
        if (data.data[i] <= 30 && data.data[i+1] <= 30 && data.data[i+2] <= 30) {
            continue;
        }
        if (data.data[i] > 30) {
            count[0]++;
            rgb.r += data.data[i];
        }
        count[1]++;
        count[2]++
        rgb.g += data.data[i+1];
        rgb.b += data.data[i+2];
    }

    // ~~ used to floor values
    rgb.r = ~~(rgb.r/count[0]);
    rgb.g = ~~(rgb.g/count[1]);
    rgb.b = ~~(rgb.b/count[2]);

    return `rgb(${rgb.r},${rgb.g},${rgb.b})`;

}