import VueRouter from 'vue-router'

// some components
import MyPM from '../components/MyPM'
import PmDetail from '../components/pm_panel/pmDetail'

export default new VueRouter({
    mode: 'hash',
    routes: [ {
            path: '/home',
            name: 'home',
            component: MyPM,
            children: [ {
                    name: 'pmdetails',
                    path: 'pm',
                    component: PmDetail,
                }
            ]
        },
        { path: '/', redirect: '/home' }
    ]
})