<template>
    <div>
        <CB v-for="(item, index) in detail" :key="index" :cdata="item"/>
    </div>
</template>

<script>
import CB from '/src/components/assets/controlButton.vue'
export default {
    name: 'Guild',
    components: {
        CB
    },
    data() {
        return {
            detail: [{
                name: '攻',
                id: 'attack',
                default: 0.15,
                min: 0,
                max: 0.3,
                step: 0.01,
                showas: 'percent',
            }, {
                name: '防',
                id: 'defend',
                default: 0.15,
                min: 0,
                max: 0.3,
                step: 0.01,
                showas: 'percent',
            }, 
            ]
        }
    },
    created() {
        let x = localStorage.getItem('gh');
        let y = {a:0.15,d:0.15};
        if (x === null) {
            localStorage.setItem('gh', JSON.stringify(y));
        } else {
            y = JSON.parse(x);
            this.detail[0].default = y.a;
            this.detail[1].default = y.d;
        }
        this.$store.commit('GH', y);
    }
}
</script>

