<template>
    <div class="myscroll" :style="{height: pHeight+'px'}">
        <ul>
            <li class="list_li" v-for="item in listdata" :key="item.pid" @click="submitElf(item.pid)">
                <div class="left_div">
                    <img :src="getImg(item.img)">
                    <ul class="type_ul">
                        <li class="type_li" v-for="(type, index) in item.types" 
                            :key="index" :style="{backgroundColor: tmap(type)}">{{type}}</li>
                    </ul>
                </div>
                <span class="mainItem">{{item.name}}</span>
                <div class="bookmark" :style="{'border-color': dmap(item.discount)}"><span class="discount">{{item.discount}} 折</span></div>
            </li>
        </ul>
    </div>
</template>

<script>
import { type_color, discount_color } from '../colorSet';

export default {
    name: "MyList",
    props: ['pHeight',],
    data() {
        return {
            listdata: [],
            TYmap: type_color,
            dMap: discount_color,
        }
    },
    methods: {
        tmap(t) {
            return this.TYmap.get(t);
        },
        dmap(t) {
            return this.dMap.get(t);
        },
        getImg(url) {
            return '/img_smallX/'+url;
        },
        submitElf(k) {
            this.$bus.$emit('show_right');
            if (this.$route.name === 'home') {
                this.$router.push({
                    name:'pmdetails',
                    query: {
                        id:k
                    }
                })
            } else {
                this.$router.replace({
                    name:'pmdetails',
                    query: {
                        id:k
                    }
                })
            }
            this.$store.commit('PID', k);
        }
    },
    beforeMount() {
        this.$bus.$on('listjson', (d) => {
            this.listdata = d;
        })
    },
    beforeDestroy() {
        this.$bus.$off('listjson')
    }
}
</script>

<style scoped>
.myscroll {
    overflow-x: hidden;
    overflow-y: auto;
}
ul {
    margin: 0;
    padding: 0;
}
.list_li {
    margin: 0;
    padding: 5px 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: row;
    align-content: center;
    cursor: pointer;
    color: var(--d10);
    background-color: var(--l6, '#d9e3ea');
    border-bottom: 1px dashed #919191;
    position: relative;
}
.list_li:hover {
    color: white;
    background-color: var(--d7, 'darkslategray');
}
.mainItem {
    display: block;
}
.discount {
    display: block;
    min-width: 2em;
    text-align: right;
    font-size: 0.8em;
    margin-right: 0.4em;
}
.left_div {
    display: flex;
    justify-content: space-between;
    width: 100px;
    align-items: center;
    align-content: center;
    flex-direction: row;
}
.left_div img {
    width: 30px;
    height: 30px;
}
.type_ul {
    display: flex;
}
.type_li {
    font-size: 0.8em;
    display: block;
    width: 2.5em;
    color: white;
    border-radius: 5px;
}

/* discount bookmark */
.bookmark {
    color: white;
    display: inline-flex;
    height: 0px;
    border: 0.5em solid;
    border-color: var(--l7);
    border-right-color: transparent !important;
    content: '';
    box-shadow: 0 5px 5px -5px #000;
    align-items: center;
    justify-content: flex-end;
}

</style>