<template>
    <div class = "switch_b" :class="limit === false && big ? 'sy': 'sn' " >
        <template v-for="(item, index) in rdata.text">
            <!-- radio button -->
            <input v-if="limit === false" :key="index"
                :id="'radio_'+rdata.short+'_'+index"
                type="radio"
                :name="'switch_'+rdata.short"
                :value="index" v-model="picked"
                @change="sendRadio(index)"
                :class="{'checked_c': picked === index}"
            />
            <!-- multiple select -->
            <input v-else :key="index" :id="'radio_'+rdata.short+'_'+index" 
               type="checkbox" :value="index" v-model="picks" @change="sendCheckbox"
               :disabled="picks.length >= +limit && picks.indexOf(index) === -1"
               :class="{'checked_c': picks.indexOf(index) >= 0}" />
            <label :key="index+'_l'" :for="'radio_'+rdata.short+'_'+index" >{{item}}</label>
        </template>
    </div>
</template>

<script>
export default {
    name: 'RadioButton',
    props: ['rdata', 'limit', 'big'],
    data() {
        return {
            picked: this.rdata.checked,
            picks: [],
        }
    },
    methods: {
        sendRadio(v) {
            if (this.limit === false) { // radio
                this.$emit("radioChanged", v);
                this.$store.dispatch("radio_val", [this.rdata.short, v]);
            } else {
                console.log(this.picks);
            }
        },
        sendCheckbox() {
            this.$store.dispatch("checked_val", [this.rdata.short, this.picks]);
        }
    },
    watch: {
        "$route.query.id": function() {
            if (this.rdata.short === 'effort') {
                this.picks = [];
                this.sendCheckbox();
            }
        }
    },
    mounted() {
        this.$store.dispatch("radio_val", [this.rdata.short, this.picked]);
    }
}
</script>

<style scoped>
.switch_b {
    display: inline-flex;
    overflow: hidden;
    padding: 2px;
}
.switch_b input {
    position: absolute;
    clip: rect(0, 0, 0, 0);
    height: 1px;
    width: 1px;
    border: 0;
    overflow: hidden;
}
.switch_b label {
    background-color: var(--l5, '#e4e4e4');
    color: var(--d9);
    line-height: 1;
    text-align: center;
    margin-right: -1px;
    border: 1px solid rgba(0, 0, 0, 0.2);
    box-shadow: inset 0 1px 3px rgba(0, 0, 0, 0.3), 0 1px rgba(255, 255, 255, 0.1);
    transition: all 0.1s ease-in-out;
}
.sy label {
    font-size: 14px;
    padding: 8px 16px;
}
.sn label {
    font-size: 12px;
    padding: 6px 10px;
}
.switch_b label:hover {
    cursor: pointer;
}
.switch_b input:checked + label {
    color: white;
    background-color: var(--d2, '#2196F3');
    box-shadow: none;
}
.switch_b label:first-of-type {
    border-radius: 16px 0px 0 16px;
}
.switch_b label:last-of-type {
    border-radius: 0 16px 16px 0;
}

/* .checked_c + label {
    background-color: #4f6679 !important;
} */
</style>