    --l4:#aecaff;
    --l3:#9cc0ff;
    --l2:#88b6ff;
    --l1:#71aaff;
    --l0:#539eff;
    --d1:#4f96f2;
    --d2:#4a8de4;
    --d3:#4584d5;
    --d4:#407ac6;
    --d5:#3b70b4;
    --d6:#3464a1;
    --d7:#2d578c;
    --d8:#254772;
    --d9:#1a3251;
    --d10:#000000;
}


