<template>
    <div>
        <div class="oooter">
            <div class="searchOuter">
                <input type="text" :placeholder="placeHolder" v-model="myIpt" @keyup.enter="checkinput">
                <div class="mysvg" v-html="svg" @click="checkinput"></div>
            </div>
            <Radio :rdata="config" @radioChanged="changeSort" :limit="false" :big="false" />
        </div>
        <div class="filter_result">
            <span class="fil_span">筛选</span>
            <ul>
                <li class="detail_filter" v-for="(item,index) in filters" :key="index">
                    <span>{{item.name}}</span>
                    <template v-if="item.det.short === 'ty'">
                        <MultiSelection :mdata="item.det" />
                    </template>
                    <template v-else><SelectPane :sdata="item.det" /></template>
                </li>
            </ul>
            <button @click="clearall">清空</button>
        </div>
    </div>
</template>

<script>
import Radio from '/src/components/assets/radioButton.vue'
import SelectPane from '/src/components/assets/selectPane.vue'
import MultiSelection from '/src/components/assets/multiSelection.vue'

const types = ['水', '火', '草', '岩石', '地上', '钢', '一般', '幽灵',
    '妖精', '恶', '超能', '格斗', '飞行', '毒', '虫', '龙', '电', '冰'];

export default {
    name: 'UserInput',
    components: {
        Radio, SelectPane, MultiSelection,
    },
    props: ['getIpt', ],
    data() {
        return {
            myIpt: '',
            placeHolder: '输入精灵名字',
            svg: `<svg t="1630917476820" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="2180" width="25" height="25"><path d="M953.504 908.256l-152.608-163.296c61.856-74.496 95.872-167.36 95.872-265.12 0-229.344-186.624-415.968-416.032-415.968-229.344 0-415.968 186.592-415.968 415.968s186.624 415.968 416 415.968c60.096-0.032 118.048-12.576 172.224-37.248 16.096-7.328 23.2-26.304 15.872-42.368-7.328-16.128-26.4-23.264-42.368-15.872-45.856 20.864-94.88 31.456-145.76 31.488-194.08 0-351.968-157.888-351.968-351.968 0-194.048 157.888-351.968 351.968-351.968 194.112 0 352.032 157.888 352.032 351.968 0 91.36-34.848 177.92-98.08 243.744-12.256 12.736-11.84 32.992 0.864 45.248 0.96 0.928 2.208 1.28 3.296 2.08 0.864 1.28 1.312 2.752 2.4 3.904l165.504 177.088c6.272 6.752 14.816 10.144 23.36 10.144 7.84 0 15.68-2.848 21.856-8.64C964.864 941.408 965.568 921.152 953.504 908.256z" p-id="2181" fill="#13227a"></path></svg>`,
            config: {
                short: 'sorter',
                text: ['时间↓', '时间↑', '编号↓', '编号↑'],
                checked: 0,
            },
            filters: {
                filter_0: {
                    name: '属性：',
                    det: {
                        short: 'ty',
                        names: types,
                    },
                },
                filter_2: {
                    name: '折扣：',
                    det: {
                        short: 'discount',
                        names: ['任何', 10, 9.8, 9, 8, 6.5, 5.5],
                    },
                },
            }
        
        }
    },
    methods: {
        checkinput() {
            this.$bus.$emit('inputchange', this.myIpt);
        },
        changeSort(d) {
            this.$bus.$emit('sortit', d);
        },
        clearall() {
            this.$bus.$emit('clearAll');
        }
    },
    mounted() {
        this.$emit('theInputHeight', this.$el.clientHeight);
    }
}
</script>

<style scoped>
.oooter {
    display: flex;
    width: 100%;
    justify-content: space-between;
    padding: 0.2em 0.8em;
    box-sizing: border-box;
}
.searchOuter {
    display: inline-flex;
    justify-content: center;
    flex-direction: row;
    align-content: center;
    align-items: center;
    padding: 0 0.2em 0 0.8em;
    border-radius: 15px;
    background-color: var(--l8, 'white');
}
.searchOuter input {
    color: var(--d10);
    width: 150px;
    text-align: center;
    border: none;
    background-color: transparent;
}
.searchOuter input:focus {
    outline-width: 0;
}
.mysvg {
    cursor: pointer;
}

.filter_result {
    display: flex;
    width: 100%;
    padding: 0.1em 0.5em 0.5em 0.5em;
    box-sizing: border-box;
    align-items: center;
    align-content: center;
    justify-content: space-between;
    flex-direction: row;
    color: var(--d6);
}
.filter_result ul {
    margin: 0;
    padding: 0;
    width: -webkit-fill-available;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
}
.detail_filter {
    display: inline-flex;
    flex-direction: row;
    align-items: center;
    align-content: center;
}
.detail_filter span {
    display: inline-block;
}
.fil_span {
    display: inline-block;
    width: 3em;
}
.filter_result button {
    display: inline-block;
    width: 4em;
    border: none;
    border-radius: 10px;
    color: var(--l10);
    cursor: pointer;
    background-color: var(--d3, 'skyblue');
}
</style>